@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arizonia&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: 0;
    list-style: none;
    outline: 0;
    text-decoration: none;
}
:root{
      --color-bg: rgb(31, 31, 56);
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 225, 0.7);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.884);
    --color-focus: red;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;

}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    /* display: none; */
}
body{
    font-family: 'Poppins', sans-serif;
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,), linear-gradient(to right, rgb(34, 34, 80), rgb(21, 21, 100));
}
.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    padding-top: 2rem;
    padding-bottom: 2rem;
}
section > h2,
section > h5{
    color: rgba(255, 255, 255, 0.884);
    color: var(--color-light);
    text-align: center;
}
section > h2{
    color: #fff;
    color: var(--color-white);
    margin-bottom: 3rem;
}
.text-light{
    color: rgba(255, 255, 255, 0.884);
    color: var(--color-light);
}
a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
a:hover{
    color: #fff;
    color: var(--color-white);
}
.btn{
    width: -webkit-max-content;
    width: max-content;
    min-width: 7rem;
    color: #4db5ff;
    color: var(--color-primary);
    display: inline-block;
    padding: 0.75rem 1.2rem;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    border-radius: 0.4rem;
    cursor: pointer;
    transition: all 400ms ease;
    transition: var(--transition);
}
.btn:hover{
    color: rgb(31, 31, 56);
    color: var(--color-bg);
    background:#fff;
    background:var(--color-white);
    border-color: transparent;
}
.btn-primary{
    color: rgb(31, 31, 56);
    color: var(--color-bg);
    background: #4db5ff;
    background: var(--color-primary);
}
.btn1{
  width: -webkit-max-content;
  width: max-content;
  min-width: 7rem;
  /* color: var(--color-primary); */
  color: rgb(31, 31, 56);
  color: var(--color-bg);
  display: inline-block;
  padding: 0.75rem 1.2rem;
  border: 1px solid rgb(31, 31, 56);
  border: 1px solid var(--color-bg);
  border-radius: 0.4rem;
  cursor: pointer;
  transition: all 400ms ease;
  transition: var(--transition);
}
.btn1:hover{
  color: rgb(31, 31, 56);
  color: var(--color-bg);
  background:#fff;
  background:var(--color-white);
  border-color: transparent;
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}
.focus{
    color: red;
    font-weight: bold;
}

/* ########################## Media Query Mediam Device ######################### */
@media screen and (max-width: 1024px) {
    .container{
        width: 86%;
        width: var(--container-width-md);
    }
    section{
        margin-top: 2rem;
    }
    
}
/* ########################## Media Query Small Device ######################### */
@media screen and (max-width: 600px) {
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    section > h2{
        margin-bottom: 2rem;
    }
    
}
.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}
.portfolio_item{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.portfolio_item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}
.portfolio_item_image{
    border-radius: 1.5rem;
    overflow: hidden;
}
.portfolio_item h3{
    margin: 1.1rem 0 1.8rem;
}
.portfolio_item_link{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    padding-bottom: 0.5rem;
}

/* ########################## Media Query medium Device ######################### */
@media screen and (max-width: 1024px) {
    .portfolio_container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

/* ########################## Media Query Small Device ######################### */
@media screen and (max-width: 666px) {
    .portfolio_container{
        grid-template-columns: 1fr;
        grid-gap: 0.1rem;
        gap: 0.1rem;
    }
    .portfolio_item{
        margin: 1rem;
    }
}
/* header { */
