/* } */

.header-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    position: relative;
}

.header-socials {
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    align-items: center;
    flex-direction: column;
    position: fixed;
    left: 4rem;
    bottom: 8rem;
}

.header-socials::before {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.header-socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


.scrolldown {
    position: fixed;
    right: 2rem;
    bottom: 8rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-size: 0.9rem;
    font-weight: 200;
}

.hello {
    font-size: 1.5rem;
}

.autoType {
    font-weight: bold;
    color: var(--color-focus);
}
.name {
    font-family: 'Arizonia', cursive;
    font-weight: bold;
    font-size: 2.9rem;
    color: var(--color-focus);
}
.headerWritten{
    font-size: 2.5rem;
    padding: 1rem 0rem 2rem 0rem;
}
.headerMe{
    color: var(--color-light);
    width: 57%;
    line-height: 2.5rem;
    font-size: 1.2rem;
}
.sizeCheck{
    font-size: 1.8rem;
}
/* ########################## Big Media Query medium Device ######################### */
@media screen and (max-width: 1250px) {
    header {
        height: -webkit-max-content;
        height: max-content;
        /* height: 90vh; */
    }

    .scrolldown {
        right: -2rem;
        bottom: 8rem;
    }
    .header-socials {
        left: 2rem;
    }
   
    
}
/* ########################## Media Query medium Device ######################### */
@media screen and (max-width: 1180px) {
    .myImg1 {
        width: 11rem;
        height: 17rem;
        position: absolute;
        right: 2rem;
        top: 12rem;
    }
    .myImg1::before{
        content: '';
        position: absolute;
        height: 9rem;
        width: 3rem;
        border-top: 6px solid var(--color-focus);
        border-left: 6px solid var(--color-focus);
        left: 0rem;
        top: -2rem;
    }
    .myImg1::after{
        content: '';
        position: absolute;
        height: 9rem;
        width: 3rem;
        border-bottom: 6px solid var(--color-focus);
        border-right: 6px solid var(--color-focus);
        right: -1rem;
        bottom: 4rem;
    }
    
}
/* ########################## Media Query medium Device ######################### */
@media screen and (max-width: 1024px) {
    header {
        height: -webkit-max-content;
        height: max-content;
        /* height: 80vh; */
    }

    .scrolldown {
        right: -3rem;
        bottom: 8rem;
    }
    .header-socials {
        left: 1rem;
    }
  
}


/* ########################## Media Query Small Device ######################### */
@media screen and (max-width: 700px) {
    header {
        height: -webkit-max-content;
        height: max-content;
        /* height: 80vh; */
    }

    .header-socials,
    .scrolldown {
        display: none;
    }

    .download {
        left: -1rem;
    }

   

    .logo1 {
        margin-bottom: 1rem;
    }
    .autoType{
        font-size: 2.5rem;
    }
    .myImg1{
        display: none;
    }
    .headerMe{
        width: 100%;
        font-size: 1.2rem;
        padding-top: 1rem;
    }
    .hello {
        font-size: 1.2rem;
    }
    .name {
        font-size: 1.8rem;
    }
}
@media screen and (max-width: 550px) {
    .dataTable {
        flex-direction: column;
        grid-gap: 0rem;
        gap: 0rem;
    }
}
@media screen and (max-width: 600px) {
    .sizeCheck {
        font-size: 1.3rem;
    }
    .autoType {
        font-size: 1.5rem;
    }
}
@media screen and (max-width: 450px) {
    .hello {
        font-size: 1.2rem;
    }
    .name {
        font-size: 2rem;
        display: block;
    }
}
/* ########################## Media Query Small Device ######################### */
@media screen and (max-width: 400px) {
    .autoType {
        font-size: 1.3rem;
    }
    .res {
        width: 18rem;
    }
}
.scrollup {
    color: var(--color-primary);
    transition: var(--transition);
    position: fixed;
    right: 2.8rem;
    top: 8rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-size: 0.9rem;
    font-weight: 200;
}
.scrollup:hover{
    color: var(--color-white);
}
/* ########################## Big Media Query medium Device ######################### */
@media screen and (max-width: 1250px) {
    .scrollup {
        right: -1.2rem;
        top: 8rem;
    }    
}
/* ########################## Media Query medium Device ######################### */
@media screen and (max-width: 1024px) {
    .scrollup {
        right: -2.3rem;
        top: 8rem;
    }  
}

/* ########################## Media Query Small Device ######################### */
@media screen and (max-width: 700px) {
    .scrollup {
        display: none;
    }

}

footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer_logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
    /* margin-left: -6rem; */
}

.allLinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    max-width: 0 auto 3rem;
    margin-bottom: 2rem;
}

.footer_socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer_socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer_socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

footer_copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

.footer_logo {
    margin-left: -6rem;
}

.mylogo{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Roboto Slab', serif;
    margin: 2rem 0rem;
}
.mylogo_bg {
    font-size: 8rem;
    line-height: 4rem;
}

.mylogo_Front{
    color: var(--color-white);
    font-size: 1.25rem;
    height: 2rem;
    margin: 0rem 0rem 0rem -15rem;
    z-index: 2;
    background: transparent;
    /* background-color: var(--color-primary); */
    
}
.mylogo_Front:hover{
    color: black;
}

/* ########################## Media Query Small Device ######################### */
@media screen and (max-width: 600px) {
    .allLinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .footer_socials {
        margin-bottom: 2.5rem;
    }
}
